@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
    To modify the style of the navbar container div.
*/
.Navbar {
    background: #5363ac;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1.2rem;
    color: white;
}

/*
    For styling the logo inside the navbar.
*/
.navbar-logo    {
    justify-self: start;
    text-align: left;
    margin-left: -10px;
    font-size: 24px;
    cursor: pointer;
    font-family: 'Josefin Sans';
}

/*
    For listing the items/links inside the navbar
*/
.menu-list  {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 15px;
    list-style: none;
    text-align: center;
    width: 70vw;
    -webkit-justify-content: end;
            justify-content: end;
    margin-right: 2rem;
}

/*
    For styling each of the links in the navbar
*/
.nav-links  {
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
}

.nav-links:hover    {
    background-color: rgb(78, 109, 211);
    color: rgb(10, 198, 231);
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.nav-links-mobile   {
    display: none;
}


/*
    For styling the hamburger menu icons
*/
.menu-icon  {
    display: none;
}

/*
    Media queries for responsive design
*/
@media screen and (max-width: 900px)    {
    .Navbar {
        position: relative;
    }

    .menu-list  {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
        height: 640px;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .menu-list.active   {
        background: #5363ac;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links  {
        text-align: center;
        padding: 1.5rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover    {
        background-color: rgb(78, 109, 211);
        color: rgb(10, 198, 231);
        border-radius: 0;
    }

    .navbar-logo    {
        position: absolute;
        top: 0;
        left: 0;
        -webkit-transform: translate(25%, 60%);
                transform: translate(25%, 60%)
    }

    .menu-icon  {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        -webkit-transform: translate(-100%, 50%);
                transform: translate(-100%, 50%);
        font-size: 2rem;
        cursor: pointer;
    } 

    .Button {
        display: none;
    }

}


.About  {
    background-position: center;
    height: 110vh;
    width: 100vw;
    background-size: cover;
}

.about-me-text  {
    position: absolute;
    left: 10vw;
    top: 25vh;
}

.icons  {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: row;
            flex-direction: row;
}

@media screen and (max-width: 500){
    .About  {
        background-position: right;
        height: 140vh;
        width: 100vw;
        background-size: auto;
    }
}



a:hover   {
    text-decoration: underline;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

